import React, { useState } from "react";
import axios from "axios";
import "semantic-ui-css/semantic.min.css";
import { Dropdown, Button, Container, Form, Loader } from "semantic-ui-react";
import Table from "./table";

const PokerhubStats = () => {
  const [type, setType] = useState(null);
  const [category, setCategory] = useState("all");
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(null);
  const [categoryOptions, setCategoryOptions] = useState([]);

  const typeOptions = [
    { key: 1, text: "", value: 1 },
    { key: 2, text: "", value: 2 },
    { key: 3, text: "", value: 3 },
    { key: 4, text: "", value: 4 },
    { key: 5, text: "", value: 5 },
  ];

  const generateCategoryOptions = (general) => {
    const categories = general
      .filter((item) => /^\d+$/.test(item.id))
      .map((item) => ({
        key: item.id,
        text: `${item.id} ${item.name}`,
        value: item.id,
      }));
    setCategoryOptions([{ key: "all", text: "", value: "all" }, ...categories]);
  };

  const handleSearch = async () => {
    if (!type || !startDate || !endDate) {
      alert(",   .");
      return;
    }

    setLoading(true);
    try {
      const response = await axios.get(
        `https://telegram.firestorm.team/phpanel/list-files?start=${startDate}&end=${endDate}&type=${type}`
      );
      setData(response.data);
      setCategory("all");
      generateCategoryOptions(response.data.general);
    } catch (error) {
      console.error("  ", error);
      alert("   . ,  .");
    } finally {
      setLoading(false);
    }
  };

  const filterData = () => {
    if (!data) return null;
    if (category === "all") return data;

    return {
      ...data,
      general: data.general.filter((item) => item.id.startsWith(category)),
      files: data.files
        .map((file) => ({
          ...file,
          data: Object.fromEntries(
            Object.entries(file.data).filter(([key]) => key.startsWith(category))
          ),
        }))
        .filter((file) => Object.keys(file.data).length > 0),
    };
  };

  const filtered = filterData();

  return (
    <Container
      style={{
        backgroundColor: "#2A2A32",
        color: "#FFFFFF",
        padding: "20px",
        borderRadius: "5px",
        width: "100%",
      }}
    >
      <Form>
        {/*  : ,  ,   */}
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "1fr 1fr 1fr",
            gap: "20px",
            marginBottom: "20px",
            alignItems: "center",
          }}
        >
          {/*  */}
          <div style={{ display: "flex", flexDirection: "column" }}>
            <label style={{ color: "#FFFFFF", marginBottom: "5px" }}></label>
            <Dropdown
              placeholder=" "
              fluid
              selection
              options={typeOptions}
              onChange={(e, { value }) => setType(value)}
              style={{
                height: "40px",
                backgroundColor: "#FFFFFF",
                color: "#666666",
                borderRadius: "5px",
              }}
            />
          </div>

          {/*   */}
          <div style={{ display: "flex", flexDirection: "column" }}>
            <label style={{ color: "#FFFFFF", marginBottom: "5px" }}> </label>
            <input
              type="date"
              className="form-control"
              value={startDate}
              onChange={(e) => setStartDate(e.target.value)}
              style={{
                height: "40px",
                padding: "8px",
                borderRadius: "5px",
                border: "none",
                backgroundColor: "#FFFFFF",
                color: "#666666",
              }}
            />
          </div>

          {/*   */}
          <div style={{ display: "flex", flexDirection: "column" }}>
            <label style={{ color: "#FFFFFF", marginBottom: "5px" }}> </label>
            <input
              type="date"
              className="form-control"
              value={endDate}
              onChange={(e) => setEndDate(e.target.value)}
              style={{
                height: "40px",
                padding: "8px",
                borderRadius: "5px",
                border: "none",
                backgroundColor: "#FFFFFF",
                color: "#666666",
              }}
            />
          </div>
        </div>

        {/*  :     */}
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "1fr 1fr 1fr",
            justifyItems: "center",
            marginBottom: "20px",
          }}
        >
          <div></div>
          <Button onClick={handleSearch} color="blue" style={{ width: "200px", height: "40px" }}>
            
          </Button>
          <div></div>
        </div>

        {/*  :  (   ) */}
        {!loading && data && filtered && filtered.files && filtered.files.length > 0 && (
          <div
            style={{
              display: "grid",
              gridTemplateColumns: "1fr 1fr 1fr",
              gap: "20px",
              marginBottom: "20px",
              alignItems: "center",
            }}
          >
            {/*  (  ) */}
            <div style={{ display: "flex", flexDirection: "column" }}>
              <label style={{ color: "#FFFFFF", marginBottom: "5px" }}></label>
              <Dropdown
                placeholder=" "
                fluid
                selection
                options={categoryOptions}
                value={category}
                onChange={(e, { value }) => setCategory(value)}
                style={{
                  height: "40px",
                  backgroundColor: "#FFFFFF",
                  color: "#666666",
                  borderRadius: "5px",
                }}
              />
            </div>

            <div></div>
            <div></div>
          </div>
        )}
      </Form>

      {/*  */}
      {loading && <Loader active inline="centered" style={{ marginTop: "20px" }}>...</Loader>}

      {/*  */}
      {filtered && (
        <div style={{ marginTop: "20px", color: "#FFFFFF", width: "100%", overflowX: "auto" }}>
          <Table files={filtered.files} general={filtered.general} users={filtered.users} />
        </div>
      )}
    </Container>
  );
};

export default PokerhubStats;
